
import HeaderBack from '../Common/HeaderBack'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
components: {
myHeader: HeaderBack,
Group,
Checker,
CheckerItem,
Datetime,
XInput,
XAddress,
XTextarea,
PopupPicker,
},
data () {
return {
header: {
HBL_icon:"icon_back",
HBC_text:"修改检测人",
HBR_icon: " ",
HBR_show: true,
HBR_type: "addperson",
HBR_show_long:true,
HBR_text_long:"保存",
},
testPerson: {
id: '',
name: '',
sex: '',
iszz: '',
birthday: '',
height: '',
weight: '',
mobile: '',
addressValue: [],
address: '',
education: [],
},
addressData: ChinaAddressData,
educationlist: [['未上学', '小学', '初中', '高中', '大专', '大学', '硕士', '博士']],
}
},
methods: {
sendPersonFn() {
let personData = this.testPerson;
let regName = /^[a-zA-Z\u4E00-\u9FA5]+$/;
let regTel = /^1[3|4|5|7|8][0-9]{9}$/;
if (!regName.test(personData.name)) {
this.$vux.toast.show({
type: 'cancel',
text: '亲，检测人姓名填写有误哦~',
width: '16em'
});
} else if (!regTel.test(personData.mobile)) {
this.$vux.toast.show({
type: 'cancel',
text: '亲，检测人电话填写有误哦~',
width: '16em'
});
} else {

comTools.showLoading(this, '修改中...');
setTimeout((data) => {

comTools.hideLoading(this);
if (true) {
this.$vux.toast.show({
type: 'success',
text: '修改成功~',
width: '7.5em'
});
setTimeout(() => {
this.$router.go(-1);
}, 3000)
}
}, 500)
}
}
},
created () {
let perObj = this.$store.state.testPersonObj;
console.log(this.$store.state.testPersonObj);
this.testPerson = perObj;
}
}



